      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" size="mini" :rules="rules.customer_name" prop="customer_id">
          <el-select v-model="params.customer_id" filterable placeholder="请输入" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker v-model="params.business_time" type="date" placeholder="选择日期" range-separator="-" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @focus="DepartmentListInvok">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addLine" class="addBtn" size="mini">增行</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" ref="tableScroll" tooltip-effect="light" :data="TableData.list" style="width: 100%" border @selection-change="handleSelectionChange" max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌名称">
          <template slot-scope="scope">
            <span>{{scope.row.brand_name}}</span>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号">
          <template slot-scope="scope">
            <span>{{scope.row.goods_id}}</span>
          </template>
        </el-table-column>
        <el-table-column property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="light" content="请选择客户名称" placement="top" v-if="!params.customer_id">
              <el-input type="text" placeholder="请选择" disabled></el-input>
            </el-tooltip>
            <v-GoodsList v-else :scopes='scope' :vxeParams='{company_id: params.company_id}' :changeGoods='handleSelect' :showObj="{goods_id: '商品编号' , goods_name: '商品名称'}" />
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" label="返胎数量" width="120">
          <template slot-scope="scope">
            <el-input type="number" v-model="scope.row.goods_num" @change="handleEdit(scope.$index, scope.row,1)" placeholder="请输入"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="unit_name" label="单位" width="50">
          <template slot-scope="scope">
            <span>{{scope.row.unit_name}}</span>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="return_type" label="返胎类型">
          <template slot-scope="scope">
            <el-select clearable v-model="scope.row.return_type" placeholder="请选择">
              <el-option label="返新胎入库" :value="1">返新胎入库</el-option>
              <el-option label="返旧胎入库" :value="2">返旧胎入库</el-option>
            </el-select>
          </template>
        </el-table-column>

        <!-- <el-table-column property="business_time" label="业务日期">
          <template slot-scope="scope">
            <el-date-picker v-model="scope.row.business_time" value-format="yyyy-MM-dd" type="date" placeholder="选择日期">
            </el-date-picker>
          </template>
        </el-table-column> -->

        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
          <template slot-scope="scope">
            <el-input v-model="scope.row.remark" maxlength="300" placeholder="请输入" suffix-icon="el-icon-edit-outline"></el-input>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
export default {
  name: "aftersaleThreeregisterAdd",
  data() {
    return {
      authority: {},
      TableData: {
        info_data: {},
        list: []
      },
      timeValue: [],
      params: {},
      searchParams: {}, //参数传递值
      goPage: 1, //默认搜索页面
      pageSize: 30, //默认表格size
      loading: false,
      per_page: [30, 50, 100, 500],
      multipleSelection: [],
      goodsList: [],
      options4: [],
      list: []
    };
  },
  computed: {
    ...mapGetters(["threeregisterAdd_showGet", "threeregisterDefault_showGet"])
  },
  methods: {
    ...mapActions(["threeregisterDefault_show"]),
    handleSelect(value, {row}) {
      this.$set(row, "goods_id", value.goods_id);
      this.$set(row, "brand_name", value.brand_name);
      this.$set(
        row,
        "unit_name",
        value.unit_name ? value.unit_name : ""
      );
      let canAddGoods = this.TableData.list[this.TableData.list.length - 1]
        .goods_id
        ? true
        : false;
      if (canAddGoods) this.TableData.list.push({ return_type: 2 });
    },
    change(val, val2) {
      this.TableData.list[val2].brand_Name = val.goods_name.brand_name;
      this.TableData.list[val2].goods_id = val.goods_name.goods_id;
      this.TableData.list[val2].unit_name = val.goods_name.unit_name;
      this.TableData.list[val2].remark = "";
    },
    defaults() {
      this.loading = false;
      this.options4 = this.list;
    },
    handleEdit(index, val, ag) {
      if (ag == 1) {
        this.$nextTick(() => {
          val.goods_num = Math.abs(Math.ceil(val.goods_num));
        });
      } else if (ag == 0) {
        this.$nextTick(() => {
          val.purchase_price = val.purchase_price.toFixed(2);
        });
      }
    },
    companyChange(val) {
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中值
      this.$set(this.params, "customer_id", ""); // 初始化客户名称下拉选中值
      this.TableData.list = [];
    },
    goBack() {
      // wz cache
      this.$router.push({ path: "/aftersale/threeregister" });
      this.threeregisterDefault_show({
        goPage: this.threeregisterAdd_showGet.goPage,
        pageSize: this.threeregisterAdd_showGet.pageSize,
        params: this.threeregisterDefault_showGet
          ? this.threeregisterDefault_showGet.params
          : {}
      });
    },
    handleCurrentChange(val) {
      this.params.page = val;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    addLine() {
      this.TableData.list.push({
        return_type: 2
      });
      this.$nextTick(function() {
        this.$refs.tableScroll.$el.childNodes[2].scrollTop = this.$refs.tableScroll.$el.childNodes[2].scrollHeight;
      });
    },
    deletes() {
      if (this.multipleSelection.length) {
        let a = new Set(this.TableData.list);
        let b = new Set(this.multipleSelection);
        let differenceABSet = new Set([...a].filter(x => !b.has(x)));
        this.TableData.list = Array.from(differenceABSet);
      } else {
        this.$message({
          message: "请先选择要删除的数据！",
          type: "warning"
        });
      }
    },
    submit() {
      if (!this.params.customer_id) {
        this.$message({
          message: "请先选择客户名称",
          type: "error"
        });
      } else {
        let tableLen = this.TableData.list.length;
        let hasLastGoodsName = !!this.TableData.list[tableLen - 1].goods_name
          ? true
          : false; //最后一行数据是否包含商品名称
        if (!hasLastGoodsName) {
          this.TableData.list.pop();
        }
        let tire_json = this.TableData.list.map(item => {
          return {
            goods_id: item.goods_id,
            goods_num: item.goods_num ? item.goods_num : 1,
            return_type: item.return_type,
            remark: item.remark ? item.remark : ""
          };
        });
        let mustNameArr = "goods_id,goods_num,return_type".split(",");
        var flag = true;
        tire_json.forEach(item => {
          for (const name of mustNameArr) {
            if (name in item) {
              if (!item[name]) {
                flag = false;
              }
            } else {
              flag = false;
            }
            if (!flag) break;
          }
          if (!flag) {
            this.$message({
              message: "请先完善信息！",
              type: "warning"
            });
            return false;
          }
        });
        if (flag) {
          let json_info = {
            customer_id: this.params.customer_id,
            company_id: this.params.company_id,
            business_time: this.params.business_time,
            department_id: this.params.department_id,
            goods_str: JSON.stringify(tire_json)
          };
          this.$axMethod.addRegister(json_info).then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: res.data.data,
                type: "success"
              });
              this.$router.push({ path: "/aftersale/threeregister" });
            }
          });
        }
      }
    }
  },
  created() {
    let { employee_id, company_admin_id } = localStorage;
    if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        business_time: this.$publicFution.formatData(new Date())
      });
    } else {
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        department_id: Number(localStorage.department_id),
        business_time: this.$publicFution.formatData(new Date())
      });
    }
    opthionList(this, localStorage);
    this.TableData.list.push({
      return_type: 2
    });
  }
};
</script>
